@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

.greetingForm {
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 14px;
    margin: 31px 0 0 70px;
}

.inputAndButtonContainer {
    display: flex;
    flex-direction: row;
}

.error {
    color: #CC0000;
    margin-top: 8px;
}

.inputClass {

}

.inputClass:valid{

}

.input {
    font-family: 'Montserrat';
    border: 1px solid #D1D1D1;
    border-radius: 5px;
    width: 372px;
    height: 36px;
    outline: none;
}

.errorInput {
    font-family: 'Montserrat';
    border: none;
    outline: none;
    width: 372px;
    min-height: 36px;
    border: 1px solid #CC0000;
    border-radius: 5px;
    font-size: 16px;
    padding-left: 12px;
    font-weight: 500;
}

.errorInput:invalid{
   /* border: 1px solid #CC0000;
    border-radius: 5px;*/
}

.button {
    font-family: 'Montserrat';
    width: 78px;
    height: 36px;
    font-weight: 600;
    margin-left: 12px;
    border: none;
    background: #0066CC;
    opacity: 100%;
    border-radius: 3px;
    color: white;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    background: #0066CC;
    opacity: 0.5;
}

.text {
    margin-bottom: 10px;
    color: rgba(0, 0, 0, 0.5);
}

.greeting {
